/* Hide topbar for desktops */
@media (min-width: 1006px) {
  .topbar {
    z-index: 0 !important;

    .navbar {
      display: none !important;
    }
  }

  .content {
    margin-top: 0px !important;
    z-index: 1000 !important;
  }
}

body {
  padding-bottom: 0;
}

h4.left-padded-headings,
.m-b-15,
.p-l-r-10{
  padding-left: 15px;
}
td.calendar-date {
  padding-left: 15px !important;
}
ul.weather-info-panel {
  padding-left: 10px;
}
.quote-author {
  font-weight: bold;
}
.quote-text {
  font-style: italic;
}

#new-event-container .pull-right>.dropdown-menu {
  right: 25px;
  margin-top: -200px;

  .box-actions {
    width: 350px !important;
  }
}

.events-hh-mm-ss-inputs input {
  max-width: 27%;
  margin-right: 10px;
}
.events-full-width-inputs {
  width: 100%;
  margin-bottom: 15px;
}

.hourly-temp-boxes {
  margin-bottom: 10px;
  padding: 10px;
}


.topbar .topbar-left {
  text-align: left;
  padding-left: 10px;
}
i.ti-menu {
  display: inherit;
  position: absolute;
  top: 20px;
  right: 5px;
}
i.ti-menu:hover {
  cursor: pointer;
}
@media (max-width: 1005px) {
  i.ti-menu {
    display: none !important;
  }
  #sidebar-menu,
  .user-box {
    margin-left: 0px;
  }
  body {
    overflow-y: auto;
  }
}
.dashboard-card {
  padding-bottom: 5px;
}
.rss-article-content {
  white-space: nowrap;
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
}

@media (min-width: 1005px) {
  body {
    overflow-y: hidden;
  }
}

.quote-btn {
  position: absolute;
  bottom: 25px;
  right: 25px;
}
@media (max-width: 1400px) {
  .weather-data-container {
    padding-top: 1.5%;
  }
}
