    margin-top: -300px;
  }
}
@media (max-width: 1400px) {
  .weather-data-container {
    padding-top: 0;

    h4 {
      line-height: 16px;
      margin-bottom: 0;
    }
    h1 {
      line-height: 22px;
      margin-top: 0;
      margin-bottom: 15px !important;
    }
    .current-weather-icon {
      width: 30px;
      height: 30px;
    }
  }
  .hourly-temp-boxes {
    margin-bottom: 0px;
    padding: 0px 10px;
  }
}

.weather-data-container {
  padding-top: 2%;
}
.mail-empty {
  background-image: url("../../images/mail-empty.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: auto;
  -moz-background-size: auto;
}
.current-weather-icon {
